<script>
import $ from 'jquery';

export default {
    name: 'cover',
    mounted() {
        $(this.$el)
            .find('.container')
            .on('mouseover', function() {
                $('.bauble').addClass('light');
                $('.star').addClass('star-light');
            });

        $(this.$el)
            .find('.container')
            .on('mouseout', function() {
                $('.bauble').removeClass('light');
            });
    }
};
</script>

<style lang="scss">
#cover-component {
    height: 100vh;
    background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
    overflow: hidden;
    filter: drop-shadow(0 0 10px white);

    h1 {
        position: absolute;
        text-align: center;
        width: 100%;
        top: 10%;
        color: white;
        font-size: 6em;
        user-select: none;
    }

    .container {
        position: absolute;
        left: 45%;
        top: 50%;

        .socks {
            position: absolute;
            top: 80px;
            left: 10px;
            z-index: 1;
            transform: rotate(10deg);
        }

        .socks.two {
            position: absolute;
            top: 143px;
            left: -40px;
            z-index: 1;
            transform: rotate(-10deg);
        }

        .socks:hover {
            animation: 1.9s deco linear infinite;
        }

        .top {
            position: absolute;
            width: 15px;
            height: 35px;
            background: #e54b4b;
            border-radius: 5px;
        }

        .foot {
            position: absolute;
            top: 20px;
            left: 0;
            width: 25px;
            height: 15px;
            border-radius: 30px;
            background: #e54b4b;
        }

        .foot.two {
            position: absolute;
            top: 20px;
            left: -10px;
            width: 25px;
            height: 15px;
            border-radius: 30px;
            background: #e54b4b;
        }

        .tree {
            position: absolute;
        }

        .base {
            width: 210px;
            height: 10px;
            background: #ccc;
            position: absolute;
            top: 304px;
            left: -45px;
            border-radius: 100%;
        }

        .base:after {
            content: '';
            width: 20px;
            height: 20px;
            background: #84714f;
            position: absolute;
            top: -25px;
            left: 83px;
        }

        .layer {
            position: absolute;
            width: 0;
            height: 0;
            border-left: 50px solid transparent;
            border-right: 50px solid transparent;
            border-bottom: 100px solid #499f68;
        }

        .layer.two {
            position: absolute;
            top: 30px;
            left: -10px;
            border-left: 60px solid transparent;
            border-right: 60px solid transparent;
            border-bottom: 120px solid #499f68;
        }

        .layer.three {
            position: absolute;
            top: 50px;
            left: -30px;
            border-left: 80px solid transparent;
            border-right: 80px solid transparent;
            border-bottom: 160px solid #499f68;
        }

        .layer.four {
            position: absolute;
            top: 80px;
            left: -50px;
            border-left: 100px solid transparent;
            border-right: 100px solid transparent;
            border-bottom: 200px solid #499f68;
        }

        .bauble {
            z-index: 1;
            position: absolute;
            border-radius: 100%;
        }

        .bauble.one {
            top: 65px;
            width: 13px;
            height: 13px;
            background: #ffe066;
        }

        .bauble.two {
            top: 105px;
            left: -30px;
            width: 13px;
            height: 13px;
            background: #ffe066;
        }

        .bauble.three {
            top: 110px;
            left: 15px;
            width: 13px;
            height: 13px;
            background: #ffe066;
        }

        .bauble.four {
            top: 163px;
            left: -30px;
            width: 13px;
            height: 13px;
            background: #ffe066;
        }

        .deco {
            position: absolute;
            top: 130px;
            left: 30px;
            width: 50px;
            height: 20px;
            background: #ef476f;
            /*   border-radius: 100% 100% 100% 100%; */
            transform: rotate(0deg);
        }

        .line {
            z-index: 1;
            position: absolute;
            top: 70px;
            left: -43px;
            width: 70px;
            height: 0;
            background: #ffe066;
            border: 1px dashed #fff;
            transform: rotate(-30deg);
        }

        .line.two {
            top: 93px;
            left: -50px;
            width: 85px;
            height: 0;
            background: #ffe066;
            border: 1px dashed #fff;
            transform: rotate(-30deg);
        }

        .line.three {
            top: 123px;
            left: -60px;
            width: 106px;
            height: 0;
            background: #ffe066;
            border: 1px dashed #fff;
            transform: rotate(-30deg);
        }

        .line.four {
            top: 153px;
            left: -80px;
            width: 136px;
            height: 0;
            background: #ffe066;
            border: 1px dashed #fff;
            transform: rotate(-30deg);
        }

        .pressie {
            z-index: 1;
            position: absolute;
            top: 250px;
            left: 120px;
            width: 60px;
            height: 60px;
            background: #f45b69;
        }

        .cover {
            position: absolute;
            left: -3px;
            width: 66px;
            height: 10px;
            background: #f45b69;
            border-bottom: 1px solid #aaa;
        }
        .wrap {
            position: absolute;
            left: 25px;
            background: #59c3c3;
            width: 10px;
            height: 60px;
        }

        .ribbon {
            position: absolute;
            top: -30px;
            left: 10px;
            background: #fff;
            width: 12px;
            height: 25px;
            border-radius: 0 80% 20% 80%;
            border: 3px solid #59c3c3;
            transform: rotate(-10deg);
        }

        .ribbon:after {
            position: absolute;
            top: 0;
            left: 20px;
            background: #fff;
            width: 12px;
            height: 25px;
            border-radius: 0 80% 20% 80%;
            border: 3px solid #59c3c3;
            transform: rotate(60deg);
            content: '';
        }

        .pressie:hover {
            animation: 0.5s move linear infinite;
        }

        @keyframes move {
            0% {
                transform: rotate(10deg);
            }
            50% {
                transform: rotate(-30deg);
            }
            100% {
                transform: rotate(10deg);
            }
        }

        @keyframes deco {
            0% {
                transform: rotate(5deg);
            }
            50% {
                transform: rotate(-10deg);
            }
            100% {
                transform: rotate(5deg);
            }
        }

        .light {
            box-shadow: 1px 1px 10px #ccc, -2px 1px 10px #ccc, 0 -1px 10px #ccc;
        }

        .star {
            z-index: 1;
            position: absolute;
            left: -49px;
            top: -40px;
            display: block;
            color: red;
            width: 0px;
            height: 0px;
            border-right: 100px solid transparent;
            border-bottom: 70px solid #ffe066;
            border-left: 100px solid transparent;
            transform: rotate(35deg) scale(0.2);
        }
        .star:before {
            border-bottom: 80px solid #ffe066;
            border-left: 30px solid transparent;
            border-right: 30px solid transparent;
            position: absolute;
            height: 0;
            width: 0;
            top: -45px;
            left: -65px;
            display: block;
            content: '';
            transform: rotate(-35deg);
        }
        .star:after {
            position: absolute;
            display: block;
            color: red;
            top: 3px;
            left: -105px;
            width: 0px;
            height: 0px;
            border-right: 100px solid transparent;
            border-bottom: 70px solid #ffe066;
            border-left: 100px solid transparent;
            transform: rotate(-70deg);
            content: '';
        }

        .star.two {
            transform: rotate(23deg) scale(0.1);
            position: absolute;
            top: 102px;
            left: -60px;
        }
    }

    @function random_range($min, $max) {
        $rand: random();
        $random_range: $min + floor($rand * (($max - $min) + 1));
        @return $random_range;
    }

    .snow {
        $total: 200;
        position: absolute;
        width: 10px;
        height: 10px;
        background: white;
        border-radius: 50%;

        @for $i from 1 through $total {
            $random-x: random(1000000) * 0.0001vw;
            $random-offset: random_range(-100000, 100000) * 0.0001vw;
            $random-x-end: $random-x + $random-offset;
            $random-x-end-yoyo: $random-x + ($random-offset / 2);
            $random-yoyo-time: random_range(30000, 80000) / 100000;
            $random-yoyo-y: $random-yoyo-time * 100vh;
            $random-scale: random(10000) * 0.0001;
            $fall-duration: random_range(10, 30) * 1s;
            $fall-delay: random(30) * -1s;

            &:nth-child(#{$i}) {
                opacity: random(10000) * 0.0001;
                transform: translate($random-x, -10px) scale($random-scale);
                animation: fall-#{$i} $fall-duration $fall-delay linear infinite;
            }

            @keyframes fall-#{$i} {
                #{percentage($random-yoyo-time)} {
                    transform: translate($random-x-end, $random-yoyo-y) scale($random-scale);
                }

                to {
                    transform: translate($random-x-end-yoyo, 100vh) scale($random-scale);
                }
            }
        }
    }
}
</style>